@font-face {
  font-family: "RobotoCondensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2"), url("../fonts/RobotoCondensed-Regular.woff");
  font-weight: 400;
}
@font-face {
  font-family: "RobotoCondensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2"), url("../fonts/RobotoCondensed-Bold.woff");
  font-weight: 700;
}
@font-face {
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/Roboto-Medium.woff2"), url("../fonts/Roboto-Medium.woff");
  font-weight: 500;
}
@font-face {
  font-family: "PT-Mono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2"), url("../fonts/PTMono-Regular.woff");
  font-weight: 400;
}
body {
  overflow-x: hidden;
}

.page {
  margin: 0 auto;
  padding: 0;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #666666;
  background-color: #ffffff;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.container {
  max-width: 1495px;
  margin: 0 auto;
}

.page-main {
  flex-grow: 1;
}

.page-footer {
  flex-shrink: 0;
}

.button {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 5px;
  color: #ffffff;
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
  cursor: pointer;
  padding: 10px;
  letter-spacing: 0.05em;
  text-decoration: none;
  box-sizing: border-box;
}
.button:hover {
  opacity: 0.8;
}
.button:active {
  opacity: 0.5;
}

.button--consultation {
  display: flex;
  background-color: #00b2ff;
  min-height: 55px;
  width: 100%;
  max-width: 407px;
  margin-left: 98px;
  padding-top: 14px;
  padding-left: 12px;
}
@media (max-width: 1023px) {
  .button--consultation {
    max-width: 379px;
    margin-left: 48px;
  }
}
@media (max-width: 767px) {
  .button--consultation {
    display: none;
  }
}

.button--consultation-mobile {
  display: none;
}
@media (max-width: 767px) {
  .button--consultation-mobile {
    display: flex;
    max-width: 100%;
    margin-left: 0;
    background-color: #00b2ff;
    min-height: 55px;
  }
}

.button--more {
  max-width: 180px;
  min-height: 45px;
  color: #00b2ff;
  border: 1px solid #00b2ff;
}

.page-header {
  padding: 33px 35px 0;
  position: relative;
  min-height: 878px;
  margin-bottom: 109px;
  background-color: #1b2d37;
  background-image: url("../img/chip.png"), url("../img/bg-header-desktop.jpg");
  background-repeat: no-repeat;
  background-position: top 258px right 14%, center top;
  background-size: 37%, cover;
  z-index: 0;
  box-sizing: border-box;
}
@media (min-resolution: 2dppx) {
  .page-header {
    background-image: url("../img/chip@2x.png"), url("../img/bg-header-desktop@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .page-header {
    padding: 20px 25px 0;
    min-height: 685px;
    margin-bottom: 54px;
    background-image: url("../img/chip-tablet.png"), url("../img/bg-header-tablet.jpg");
    background-repeat: no-repeat;
    background-position: top 113px right 0, center top;
    background-size: 260px, cover;
  }
}
@media (max-width: 1023px) and (min-resolution: 2dppx) {
  .page-header {
    background-image: url("../img/chip-tablet@2x.png"), url("../img/bg-header-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .page-header {
    padding: 17px;
    min-height: 647px;
    margin-bottom: 37px;
    background-image: url("../img/chip-mobile.png"), url("../img/bg-header-mobile.jpg");
    background-position: top 92px right, center top;
    background-size: 294px, cover;
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .page-header {
    background-image: url("../img/chip-mobile@2x.png"), url("../img/bg-header-mobile@2x.jpg");
  }
}

.page-header__container {
  max-width: 1495px;
  margin: 0 auto;
}

.page-header__nav {
  max-width: 1410px;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  margin-bottom: 170px;
}
@media (max-width: 1023px) {
  .page-header__nav {
    margin-bottom: 110px;
  }
}
@media (max-width: 767px) {
  .page-header__nav {
    margin-bottom: 230px;
  }
}

.page-header__contacts {
  display: flex;
  align-items: center;
}
@media (max-width: 1023px) {
  .page-header__contacts {
    align-items: flex-start;
  }
}

.page-header__contacts-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 88px;
  row-gap: 20px;
}
@media (max-width: 1023px) {
  .page-header__contacts-list {
    column-gap: 57px;
    row-gap: 0;
  }
}
@media (max-width: 767px) {
  .page-header__contacts-list {
    column-gap: 45px;
  }
}

.page-header__contacts-item {
  position: relative;
}

.page-header__contacts-link {
  font-family: "PT-Mono", "Arial", sans-serif;
  line-height: 18px;
  color: #ffffff;
  text-decoration: none;
  display: inline-block;
  padding-left: 45px;
}
@media (max-width: 1023px) {
  .page-header__contacts-link {
    font-size: 0;
    padding-left: 0;
  }
}
.page-header__contacts-link::before {
  content: "";
  position: absolute;
  left: 0;
  display: flex;
  align-items: center;
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-position: center;
}
@media (max-width: 1023px) {
  .page-header__contacts-link::before {
    width: 24px;
    height: 24px;
    left: auto;
    right: 0;
  }
}
.page-header__contacts-link--email::before {
  background-image: url("../img/icon-email.svg");
}
@media (max-width: 1023px) {
  .page-header__contacts-link--email::before {
    width: 26px;
    background-size: 24px 19px;
  }
}
.page-header__contacts-link--phone::before {
  background-image: url("../img/icon-phone.svg");
}
@media (max-width: 1023px) {
  .page-header__contacts-link--phone::before {
    background-size: 24px 24px;
  }
}

.page-header__button {
  min-width: 180px;
  min-height: 45px;
  border: 1px solid #ffffff;
  font-size: 14px;
  line-height: 16px;
  margin-left: 38px;
}
@media (max-width: 1023px) {
  .page-header__button {
    display: none;
  }
}

.page-header__logo {
  width: 52px;
  height: 52px;
  background: url("../img/icon-logo.svg") no-repeat center/contain;
}
@media (max-width: 767px) {
  .page-header__logo {
    width: 38px;
    height: 38px;
    margin-right: auto;
  }
}
.page-header__logo p {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  margin: 0;
  margin-left: 65px;
  display: inline-block;
  width: 200px;
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  color: #ffffff;
  padding-top: 5px;
}
@media (max-width: 1023px) {
  .page-header__logo p {
    margin-left: 68px;
  }
}
@media (max-width: 767px) {
  .page-header__logo p {
    font-size: 0;
  }
}
.page-header__logo p:nth-child(2) {
  font: 500 12px/14px "Roboto", "Arial", sans-serif;
  color: #788891;
  padding: 0;
}
@media (max-width: 767px) {
  .page-header__logo p:nth-child(2) {
    font-size: 0;
  }
}

.promo {
  color: #ffffff;
  padding-left: 37px;
}
@media (max-width: 1023px) {
  .promo {
    padding-left: 65px;
    padding-top: 5px;
    position: relative;
    margin: 0;
  }
}
@media (max-width: 767px) {
  .promo {
    padding-left: 0;
    padding-top: 0;
    justify-content: center;
  }
}

.promo__info {
  padding-top: 54px;
  min-width: 770px;
}
@media (max-width: 1023px) {
  .promo__info {
    min-width: auto;
    max-width: 61%;
    padding-top: 0;
  }
}
@media (max-width: 767px) {
  .promo__info {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 280px;
  }
}

.promo__info h1 {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 80px;
  font-weight: 700;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-align: left;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 31px;
}
@media (max-width: 1023px) {
  .promo__info h1 {
    font-size: 60px;
    line-height: 70px;
  }
}
@media (max-width: 767px) {
  .promo__info h1 {
    font-size: 40px;
    line-height: 47px;
    margin-bottom: 26px;
  }
}

.promo__info p {
  margin: 0;
  font-family: "PT Mono", "Arial", serif;
  font-size: 22px;
  line-height: 34px;
  text-transform: lowercase;
  max-width: 690px;
  position: relative;
  padding-left: 92px;
  margin-bottom: 42px;
}
@media (max-width: 1023px) {
  .promo__info p {
    max-width: 356px;
    font-size: 18px;
    line-height: 28px;
    padding-left: 48px;
    z-index: 3;
    margin-bottom: 55px;
  }
}
@media (max-width: 767px) {
  .promo__info p {
    font-size: 16px;
    line-height: 25px;
    margin-bottom: 40px;
  }
}
.promo__info p::before {
  content: "";
  position: absolute;
  width: 66px;
  height: 2px;
  background-color: #ffffff;
  left: 0;
  top: 15px;
}
@media (max-width: 1023px) {
  .promo__info p::before {
    width: 33px;
  }
}

.promo__info a {
  font-size: 16px;
  line-height: 19px;
}

.promo__image {
  padding-left: 18px;
}
@media (max-width: 1023px) {
  .promo__image {
    padding-left: 0;
    z-index: -1;
    width: 465px;
    position: absolute;
    margin-left: 58%;
    top: -71px;
  }
}
@media (max-width: 767px) {
  .promo__image {
    width: 104%;
    margin-left: 0;
    top: -197px;
    left: 9px;
  }
}
.promo__image img {
  max-width: fit-content;
}
@media (max-width: 1023px) {
  .promo__image img {
    max-width: 100%;
  }
}

.about {
  position: relative;
  max-width: 1220px;
  width: 100%;
  padding: 0 30px 110px 205px;
  color: #666666;
  box-sizing: border-box;
  background-image: url("../img/year.svg");
  background-repeat: no-repeat;
  background-size: 220px 317px;
  background-position: left top;
}
@media (max-width: 1023px) {
  .about {
    padding: 8px 30px 45px 212px;
    background-size: auto;
    background-position: left top 6px;
  }
}
@media (max-width: 767px) {
  .about {
    margin-bottom: 65px;
    padding: 0 15px 0 17px;
    background-image: none;
  }
}

