  }
}

.benefits {
  padding: 0 25px;
  margin-bottom: 80px;
}
@media (max-width: 1023px) {
  .benefits {
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .benefits {
    padding: 0 16px;
    margin-bottom: 37px;
  }
}

.benefits::before {
  content: "";
  position: absolute;
  background-color: #ffffff;
  background-image: url("../img/triangles-benefits-desktop.svg");
  background-repeat: no-repeat;
  background-position: left bottom 50px;
  background-size: cover;
  z-index: -1;
  width: 100%;
  height: 1260px;
  margin-left: -25px;
}
@media (max-width: 1023px) {
  .benefits::before {
    background-image: url("../img/triangles-benefits-tablet.svg");
